<template>
  <div class="home">
    <div class="heade">
      <van-icon name="arrow-left" @click="go" />
      <van-search
        v-model="value"
        shape="round"
        placeholder="请输入搜索关键词"
        input-align="center"
      />
      <van-icon name="sort" @click="qh" />
    </div>
    <div class="box">
      <div class="card" v-for="index in list" :key="index.id">
        <img class="ico" :src="index.icon" />
        <div>{{ index.title }}</div>
      </div>
    </div>
    <div class="zhuti">
      <van-loading v-if="!zt"  color="#1989fa" size="50px"/>
      <ul class="card1" v-if="zt">
        <li
          v-for="(item, value) in imagesNameArr"
          :key="value"
          class="cardli1"
          @click="spxq(value)"
        >
          <div class="commodity">
            <img
              :src="require(`../../public/ldl_tp/` + item.img)"
              alt="商品"
              :class="item.gd"
            />
            <ul>
              <li>
                <div class="describe">wenben</div>
              </li>
              <li>
                <span class="price">￥10</span>
                <span class="rebate">送: 20</span>
              </li>
              <li>
                <span class="laber1">平台自营</span>
                <span class="laber2">包邮</span>
              </li>
            </ul>
          </div>
        </li>
      </ul>
    </div>
  </div>
</template>

<script>
import Wntj from "@/components/Wntj";
export default {
  components: {
    Wntj,
  },
  data() {
    return {
      value: "",
      search: "",
      list: [
        { id: 1, icon: require("../../public/bj1.jpg"), title: "批发市场" },
        { id: 2, icon: require("../../public/bj1.jpg"), title: "批发" },
        { id: 3, icon: require("../../public/bj1.jpg"), title: "批发" },
        { id: 4, icon: require("../../public/bj1.jpg"), title: "批发" },
        { id: 5, icon: require("../../public/bj1.jpg"), title: "社交" },
        { id: 6, icon: require("../../public/bj1.jpg"), title: "批发" },
        { id: 7, icon: require("../../public/bj1.jpg"), title: "批发" },
        { id: 8, icon: require("../../public/bj1.jpg"), title: "零售市场" },
      ],
      imagesNameArr: [],
      fl: this.$store.state.Status.code,
      kgd: "",
      zt: false,
    };
  },
  methods: {
    go() {
      this.$router.go(-1);
    },
    qh() {
      this.$store.commit("Status/setStatus", true);
      console.log(this.$store.state.Status.code);
    },
    spxq(value) {
      this.$router.push(`/spxq/${value}`);
    },
  },
  mounted() {
    const cs = this.$store.state.Status.code;
    let requireModule = require.context("../../public/ldl_tp/", true, /\.jpg$/);
    for (var i = 0; i < requireModule.keys().length; i++) {
      let imagename = requireModule
        .keys()
        [i].substr(2, requireModule.keys()[i].length);
      // console.log(imagename)
      if (imagename.slice(0, 2) == cs) {
        const imgA = new Image();
        imgA.src = require("../../public/ldl_tp/" + imagename);
        imgA.onload = () => {
          const gd = imgA.height;
          this.imagesNameArr.push({ img: imagename, height: gd });
        };
      }
    }

    setTimeout(() => {
      this.imagesNameArr.sort((a, b) => {
        return a.height - b.height;
      });

      this.imagesNameArr = this.imagesNameArr.map((item) => {
        if (item.height < 900) {
          this.kgd = "imgstaus1";
        } else if (item.height < 1200) {
          this.kgd = "imgstaus2";
        } else if (item.height < 1500) {
          this.kgd = "imgstaus3";
        } else if (item.height < 1700) {
          this.kgd = "imgstaus4";
        } else {
          this.kgd = "imgstaus5";
        }
        return { img: item.img, height: item.height, gd: this.kgd };
      });
      this.zt = true;
    }, 1000);
  },
};
</script>

<style scoped lang="less">
.heade {
  display: flex;
  flex-flow: row nowrap;
  background-color: #ccc;
}

.van-icon {
  height: 34px;
