  background-color: #ccc;
  border-radius: 10px;
  align-content: center;
  line-height: 34px;
  margin: 10px;
}
.van-search {
  width: 80%;
  background-color: #ccc;
}
.box {
  display: flex;
  flex-flow: row wrap;
  font-size: 15px;
}
.card {
  width: 25%;
}
.ico {
  height: 50px;
  width: 50px;
  margin: 15px 0 5px 0;
  border-radius: 10px;
}

ul,
li {
  list-style: none;
  margin: 0;
  padding: 0;
}
.wntj {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: flex-start;
  height: 40px;
  background-color: #f6f9ed;
  // padding-left: 36vw;
  line-height: 40px;
  span {
    padding: 0 10px;
    color: rgba(255, 30, 0, 0.842);
    font-weight: bolder;
  }
}
.zhuti {
  margin-bottom: -10px;
}
.card1 {
  display: flex;
  flex-flow: row wrap;
  margin-bottom: 92px;
  background-color: #f6f9ed;
  justify-content: flex-start;
  // column-gap: 10px;
  // column-count: 2;
  .cardli1 {
    // break-inside: avoid;
    max-width: 46vw;
    border: 1px solid #ccc;
    border-radius: 10px;
    background-color: aliceblue;
    margin: 5px 0 0 10px;
  }
  .commodity li {
    display: flex;
    align-items: flex-end;

    margin-bottom: 4px;
  }
  .commodity span {
    margin-left: 2px;
  }
}
.imgstaus1 {
  width: 46vw;
  height: 36vw;
  border-radius: 5px 5px 0 0;
}
.imgstaus2 {
  width: 46vw;
  height: 46vw;
  border-radius: 5px 5px 0 0;
}
.imgstaus3 {
  width: 46vw;
  height: 62vw;
  border-radius: 5px 5px 0 0;
}
.imgstaus4 {
  width: 46vw;
  height: 70vw;
  border-radius: 5px 5px 0 0;
}
.imgstaus5 {
  width: 46vw;
  height: 82vw;
  border-radius: 5px 5px 0 0;
}

.describe {
  text-align: left;
  margin-left: 2px;
  font-size: 15px;
}
.price {
  color: darkgoldenrod;
  font-size: 18px;
}
.rebate {
  color: rgba(255, 0, 0, 0.959);
  font-size: 15px;
  padding: 2px;
}
.laber1 {
  font-size: 12px;
  background-color: rgba(255, 0, 0, 0.959);
  color: white;
  border: 1px solid #ccc;
  padding: 1px;
  border-radius: 5px;
}
.laber2 {
  box-sizing: border-box;
  font-size: 12px;
  color: rgba(255, 145, 0, 0.959);
  border: 2px solid rgba(255, 145, 0, 0.959);
  border-radius: 5px;
  margin-top: 2px;
}
</style>
